.header {
    $fs: 60;
    $fs-mb: 33;
    $fs-mb-lp: 45;

    font-size: vw($fs);
    width: 100vw;
    min-height: em(800, $fs);
    position: relative;

    @media (--viewport-mobile) {
        font-size: vw-vh-contain($fs-mb, true);
    }

    @media (--viewport-mobile-lp) {
        font-size: vh($fs-mb-lp, true);
    }

    &__decor,
    .decor__el {
        position: absolute;
        top: 0;
        right: 0;
        background-size: contain;
        background-position: bottom right;
        background-repeat: no-repeat;
    }

    &__decor {
        left: 0;
        bottom: -2px;
        background-image: url("/assets/images/icons/bg/wave-top.svg");

        @media (--viewport-mobile-pt) {
            background-size: 200%;
        }

        @media (--viewport-mobile) {
            background-position: bottom left;
        }
    }

    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: em(22, $fs);
        min-height: em(572, $fs);
        padding-top: 0;

        @media (--viewport-mobile) {
            flex-direction: column;
            padding-top: em(60, $fs-mb);
        }

        @media (--viewport-mobile-lp) {
            padding-bottom: em(52, $fs-mb-lp);
        }

        &-decor {
            position: relative;

            .decor__el {
                width: em(696, $fs);
                height: em(572, $fs);
                top: 50%;
                transform: translateY(-35%);
                background-image: url("/assets/images/icons/header/laptop-bg.png");

                @media (--viewport-mobile) {
                    position: relative;
                    transform: translateY(em(-12, $fs-mb));
                    margin: 0 auto;
                    background-position: top center;
                    width: em(324, $fs-mb-lp);
                    height: em(266, $fs-mb-lp);
                    background-image: url("/assets/images/icons/header/laptop-bg-mb.png");
                }
            }
        }
    }

    &__el {
        width: 50%;

        @media (--viewport-mobile) {
            width: 100%;
        }
    }

    &__title {
        @media (--viewport-mobile) {
            font-weight: $font-weight-regular;
        }

        .h1 {
            font-family: $font-accent;
            font-size: em(60, $fs);
            color: $color-text-light;
            font-weight: $font-weight-regular;

            @media (--viewport-mobile) {
                font-size: em(40, $fs-mb);
            }

            span {
                line-height: em(80, $fs);
                color: $color-text-accent;

                @media (--viewport-mobile) {
                    display: inline-block;
                    font-size: em(33, 40);
                    margin-top: em(5, 33);
                }
            }
        }

        .subtitle {
            margin-top: em(60, $fs);
            padding-right: em(210, $fs);
            font-size: em(28, $fs);
            line-height: em(42, 28);
            color: $color-text-light;

            @media (--viewport-mobile) {
                font-size: em(16, $fs-mb);
                line-height: em(20, 16);
                font-weight: $font-weight-default;
                padding-right: 0;
                margin-top: em(30, 16);
                text-align: center;
            }
        }

        @media (--viewport-mobile) {
            text-align: center;
        }
    }
}
