.footer {
    $fs: 18;
    $fs-mb: 16;
    $fs-mb-lp: 30;

    font-size: vw($fs);
    width: 100vw;
    min-height: em(1046, $fs);
    position: relative;

    @media (--viewport-mobile) {
        font-size: vw-vh-contain($fs-mb, true);
    }

    @media (--viewport-mobile-lp) {
        font-size: vh($fs-mb-lp, true);
    }

    &__wrap {
        padding-left: em(155, $fs);
        padding-right: em(155, $fs);
        position: relative;

        @media (--viewport-mobile) {
            padding-left: vw-vh-contain(30, true);
            padding-right: vw-vh-contain(30, true);
            padding-bottom: vw-vh-contain(10, true);
        }

        @media (--viewport-mobile-lp) {
            padding-top: vw(40, true);
        }

        @media (width > 1440px) {
            width: vw(1440);
            margin: 0 auto;
            padding-top: vw-vh-contain(153);
        }

        @media (width > 1440px) and (height < 678px) {
            padding-top: vh(350);
        }
    }

    &__decor {
        position: absolute;
        top: -2px;
        right: 0;
        left: 0;
        bottom: 0;
        background-size: contain;
        background-position: top right;
        background-repeat: no-repeat;
        background-image: url("/assets/images/icons/bg/wave-bottom.svg");

        @media (--viewport-mobile-pt) {
            background-size: 200%;
        }

        @media (--viewport-mobile) {
            background-position: top left;
        }
    }

    &__results {
        color: $color-text-light;
        line-height: em(24, $fs);
        text-align: center;

        &:first-of-type {
            margin-top: em(50, $fs);

            @media (--viewport-mobile) {
                margin-bottom: em(25, $fs-mb);
            }
        }

        &:nth-of-type(2) {
            margin-bottom: em(50, $fs);
        }

        @media (--viewport-mobile) {
            text-align: left;
        }

        @media (--viewport-mobile-lp) {
            text-align: center;
        }
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: stretch;
        gap: em(48, $fs) em(30, $fs);

        @media (--viewport-mobile-pt) {
            justify-content: center;
            align-items: flex-start;
            flex-direction: column;
            padding-bottom: em(30, $fs-mb);
            border-radius: em(30, $fs-mb);
            background-color: $color-text;
            gap: 0;
        }
    }

    &__item {
        width: em(244, 18);
        padding: em(28, $fs) em(32, $fs) em(24, $fs);
        background-color: $color-text;
        border-radius: em(20, $fs);
        text-align: center;

        @media (--viewport-mobile) {
            width: 100%;
            text-align: left;
            padding-left: em(24, $fs-mb);
            padding-right: em(24, $fs-mb);

            &:not(:last-of-type) {
                padding-bottom: em(20, $fs-mb);
            }

            &:not(:first-of-type) {
                padding-top: em(20, $fs-mb);
            }
        }

        .item__el {
            color: $color-text-light;

            &--analitic {
                display: flex;
                flex-direction: column;

                @media (--viewport-mobile) {
                    flex-direction: row;
                    align-items: end;
                    gap: em(10, $fs-mb);
                }
            }

            &--title {
                line-height: em(20, $fs);
                margin-bottom: em(17, $fs);

                @media (--viewport-mobile) {
                    font-size: em(18, $fs-mb);
                    margin-bottom: em(7, 18);

                    &.item__el--min {
                        margin-bottom: em(20, 12);
                    }
                }
            }

            &--value {
                font-size: em(40, $fs);
                line-height: 1;
                color: $color-text-accent;

                @media (--viewport-mobile) {
                    font-size: em(24, $fs-mb);
                }
            }

            &--type {
                @media (--viewport-mobile) {
                    color: $color-text-accent;
                }
            }

            &--type,
            &--comparison {
                line-height: em(25, $fs);

                @media (--viewport-mobile) {
                    line-height: 1;
                }
            }

            &--comparison {
                font-weight: 200;
                margin-top: em(17, $fs);

                @media (--viewport-mobile) {
                    font-size: em(14, $fs-mb);
                    margin-top: em(11, 14);
                }
            }

            &--min {
                font-size: em(12, $fs);

                @media (--viewport-mobile) {
                    font-size: em(12, $fs-mb);
                }
            }
        }
    }

    &__developer {
        font-weight: 300;
        line-height: em(45, $fs);
        color: $color-about;
        text-align: center;
        position: absolute;
        bottom: 0;
        right: em(25, $fs);

        @media (--viewport-mobile) {
            position: static;
            font-size: em(18, $fs-mb);
            margin-top: em(25, 18);
        }
    }

    .advertiser {
        margin-top: em(148, $fs);

        @media (--viewport-mobile) {
            font-size: em(18, $fs-mb);
            margin-top: em(80, 18);
        }
    }
}
