@import "base/mixins/reduced-motion.scss";
@import "base/mixins/reset.scss";
@import "mixins/font-size.scss";

/* main styles */
html,
body {
    text-size-adjust: 100%;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    box-sizing: border-box;
    text-rendering: optimizelegibility;
    font-family: $font-default;
    font-size: rem(18);
    font-weight: $font-weight-default;
    color: $color-text;
    background: $color-text;
    width: 100%;
    overflow-x: hidden;
    height: 100vh;

    @include font-size;
    @include reduced-motion;
}

@include reset;

// Remove if total buttons reset is not needed
a,
button {
    @include reset-btn;
}

b,
strong {
    /* Customizing font-weight according to design */

    /* e.g. Semi Bold */
    font-weight: $font-weight-bold;
}

.container {
    width: vw(1211 + 115 + 115);
    height: 100%;
    padding: vw(120) vw(115) vw(31);
    margin-left: auto;
    margin-right: auto;

    @media (width > 1440px) {
        width: vw(1440);
        margin: 0 auto;
    }

    @media (--viewport-mobile) {
        width: auto;
        padding: vw-vh-contain(70, true) vw-vh-contain(30, true);
    }

    @media (--viewport-mobile-lp) {
        width: auto;
        padding: vw(70, true) vw(30, true);
    }
}

.advertiser {
    font-weight: 300;
    color: $color-about;
    text-align: center;
}
