.main {
    $fs: 18;
    $fs-mb: 16;
    $fs-mb-lp: 28;

    width: 100%;
    font-size: vw($fs);
    background-color: $color-bg;

    @media (--viewport-mobile-pt) {
        font-size: vw-vh-contain($fs-mb, true);
    }

    @media (--viewport-mobile-lp) {
        font-size: vw-vh-contain($fs-mb-lp, true);
    }

    &__wrap {
        width: fit-content;
        margin: 0 auto;
        display: grid;
        grid-template-columns: em(423, $fs) em(92, $fs) em(73, $fs) em(591, $fs) em(29, $fs);
        grid-template-rows: repeat(4, 1fr);
        padding-top: em(48, $fs);
        padding-bottom: em(65, $fs);

        @media (--viewport-mobile) {
            grid-template-columns: 1fr;
            grid-template-rows: repeat(8, auto);
        }
    }

    &__block {
        &--1 {
            grid-area: 1 / 3 / 2 / 5;
            align-self: center;
            padding-right: em(53, $fs);
        }

        &--2 {
            grid-area: 1 / 1 / 2 / 2;
            align-self: end;

            .main__block-img {
                min-height: em(400, $fs);
                background-image: url("/assets/images/scammers.png");
            }
        }

        &--3,
        &--4 {
            height: em(354, $fs);
            align-self: start;
        }

        &--3 { grid-area: 2 / 1 / 3 / 2; }

        &--4 {
            grid-area: 2 / 3 / 3 / 5;
        }

        &--5 {
            grid-area: 3 / 1 / 4 / 4;
        }

        &--6 {
            grid-area: 3 / 4 / 4 / 5;

            .main__block-img {
                min-height: em(386, $fs);
                background-image: url("/assets/images/phone-viewing.png");
                background-position: bottom;

                @media (--viewport-mobile) {
                    min-height: em(246, $fs-mb);
                }
            }
        }

        &--7,
        &--8 {
            margin-top: em(20, $fs);
        }

        &--7 {
            grid-area: 4 / 4 / 5 / 6;
        }

        &--8 {
            grid-area: 4 / 1 / 5 / 4;

            .main__block-img {
                min-height: em(393, $fs);
                background-image: url("/assets/images/control.png");
                background-position: bottom left;
            }
        }

        &-text {
            .text:not(:last-of-type) {
                margin-bottom: em(27, $fs);
            }

            .text-button {
                display: inline-block;
                margin-top: em(35, $fs);
                background-color: $color-bg-accent;
                padding: em(17, $fs) em(34, $fs);
                border-radius: em(30, $fs);
                color: $color-text-light;
                text-decoration: none;
                transition: color 0.4s ease;

                @media (--viewport-mobile) {
                    display: block;
                    margin-left: auto;
                    margin-right: auto;
                    width: em(216, 18);
                    font-size: em(18, $fs-mb);
                    text-align: center;
                }

                &:hover {
                    color: $color-text-accent;
                }
            }
        }

        &-img {
            width: 100%;
            background-size: contain;
            background-position: bottom right;
            background-repeat: no-repeat;
        }

        &-charts {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;
            height: 100%;
            padding: em(38, $fs) em(55, $fs) em(25, $fs);
            background: $color-bg-accent;
            border-radius: em(48, $fs);
            color: $color-milk;

            @media (--viewport-mobile) {
                padding-left: em(27, $fs-mb);
                padding-right: em(27, $fs-mb);
            }

            .charts {
                display: flex;
                column-gap: em(50, $fs);

                @media (--viewport-mobile) {
                    flex-direction: column;
                    gap: em(30, $fs-mb);
                }

                .main__block {
                    &-chart {
                        width: 100%;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                    }

                    &-pargraph {
                        font-weight: $font-weight-bold;
                        line-height: em(25, $fs);
                        text-align: center;
                    }
                }

                .main__block-value {
                    font-family: $font-accent;
                    font-size: em(60, $fs);
                    line-height: em(90, 60);
                    position: relative;
                    width: em(153, 60);
                    height: em(153, 60);
                    margin-bottom: em(18, 60);

                    .pie {
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        background-size: contain;
                        background-position: bottom right;
                        background-repeat: no-repeat;

                        &--1 {
                            background-image: url("assets/images/pie-1.png");
                        }

                        &--2 {
                            background-image: url("assets/images/pie-2.png");
                        }

                        &--3 {
                            background-image: url("assets/images/pie-3.png");
                        }
                    }

                    .value {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                    }
                }
            }

            .main__block-common {
                margin-top: em(15, 14);
                font-size: em(14, $fs);
                font-weight: 200;
                text-align: center;

                @media (--viewport-mobile) {
                    font-size: em(14, $fs-mb);
                }
            }
        }

        @media (--viewport-mobile) {
            &--1 {
                grid-area: 1 / 1 / 2 / 2;
                padding-right: 0;
            }

            &--2 {
                grid-area: 2 / 1 / 3 / 2;
                margin-top: em(39, $fs);

                .main__block-img {
                    height: em(242, $fs-mb);
                    min-height: auto;
                    background-position: bottom center;
                }
            }

            &--3,
            &--4 {
                height: auto;
                margin-top: em(15, $fs-mb);
            }

            &--3 {
                grid-area: 3 / 1 / 4 / 2;
                margin-top: 0;
            }

            &--4 { grid-area: 4 / 1 / 5 / 2; }

            &--5 {
                grid-area: 5 / 1 / 6 / 2;
                margin-top: em(40, $fs-mb);
            }

            &--6 {
                grid-area: 6 / 1 / 7 / 2;
                margin-top: em(35, $fs-mb);
                margin-bottom: em(50, $fs-mb);
            }

            &--7,
            &--8 {
                margin-top: 0;
            }

            &--7 { grid-area: 7 / 1 / 8 / 2; }

            &--8 {
                grid-area: 8 / 1 / 9 / 2;

                .main__block-img {
                    width: em(266, $fs-mb);
                    min-height: em(270, $fs-mb);
                    background-position: center em(40, $fs-mb);
                    margin-left: auto;
                    margin-right: auto;
                }
            }
        }
    }
}
