@use "sass:math";

// colors
$color-bg: #8b9bf1;
$color-bg-accent: #b835a0;
$color-text: #1e2e85;
$color-text-accent: #ffa5ed;
$color-text-light: #fff;
$color-milk: #fef5f5;
$color-about: #000836;

// text
$font-default: "Manrope", system-ui, sans-serif;
$font-accent: "ComicCAT", system-ui, sans-serif;

// Basing Weights
$font-weight-default: 600;
$font-weight-regular: 400;
$font-weight-bold: 700;
$basePxDesktop: 18;
$baseWidthDesktop: 1440;
$baseHeightDesktop: 1080;
$max-width-desktop: 1440;

// add
$basePxMb: 16;
$baseWidthMb: 375;
$baseHeightMb: 667;

// Font sizes
// to calc 1rem with poly-fluid-sizing
$map-font-size-lp: (
    560px: 18px,
    850px: 18px,
    852px: 18px,
    1440px: 18px
);
$map-font-size-pt: (
    320px: 14px,
    375px: 16px,
    425px: 17px,
    1024px: 18px
);

// Add one more variable if mobile line-height differs from desktop one
$line-height-default: math.div(22, $basePxDesktop);

// Line height of accent font family text
$line-height-accent: math.div(126, 140);
